<template>
  <div class="place-order">
    <back-header title="确认订单" />
    <scroll clickable ref="scroll">
      <address-link :address="address"/>
      <img class="line" src="./line@3x.png" alt="">
      <div class="order" v-for="(item, index) in list" :key="item.id">
        <order-store :click="false" :store="item.store" :show-checkbox="false" :index="index"/>
        <div class="goods" v-for="(goods, idx) in item.goods" :key="idx">
          <OrderItem :par-index="index" :own-index="idx" :goods="goods" :show-checkbox="false"/>
          <div class="price">
            <p>{{ goods.price }}</p>
            <van-stepper @change="changeNum" v-model="goods.count" button-size="0.6rem" input-width="0.6rem" disable-input />
          </div>
        </div>
        <div class="freight">
          <label>运费</label>
          <span>包邮</span>
        </div>
        <div class="message">
          <label>订单留言</label>
          <input type="text" @blur.prevent="inputBlur" v-model="item.remark" placeholder="选填，建议先与商家协商一致">
        </div>
      </div>

      <van-radio-group v-model="payType" class="pay">
        <div class="pay-item" @click="payType=2">
          <img src="./alipay@3x.png" alt="">
          <p>支付宝</p>
          <div style="flex: 1"></div>
          <van-radio icon-size="0.4rem" checked-color="#E1244E" :name="2" />
        </div>
        <div class="pay-item" @click="payType=1">
          <img src="./wechat@3x.png" alt="">
          <p>微信支付</p>
          <div style="flex: 1"></div>
          <van-radio icon-size="0.4rem" checked-color="#E1244E" :name="1"/>
        </div>
      </van-radio-group>
    </scroll>
    <div class="order-float">
      <span>{{ totalPrice }}</span>
      <button @click="submit">提交订单</button>
    </div>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import OrderStore from '../../components/order-store/order-store'
import OrderItem from '../../components/order-item/order-item'
import AddressLink from '../../components/address-link/address-link'
import { getItem, removeItem } from '../../common/js/storage'
import { post } from '../../common/js/http'
import cloneDeep from '../../common/js/clone-deep'
import { mapMutations, mapState } from 'vuex'
import { blurInput } from '../../common/js/mixins'

export default {
  name: 'place-order',
  mixins: [blurInput],
  components: {
    BackHeader,
    Scroll,
    OrderItem,
    OrderStore,
    AddressLink
  },
  data() {
    return {
      list: [],
      address: {},
      payType: 2,
      totalPrice: 0,
      freight: 0,
      addressList: []
    }
  },
  created() {
    this.init()
    this.getAddressList()
  },
  activated () {
    this.init()
  },
  computed: {
    ...mapState(['routeHistory'])
  },
  methods: {
    ...mapMutations(['changeHistory']),
    getAddressList() {
      post('/shop/user/getUserAddressList')
        .then(({ data: { data } }) => {
          this.addressList = data
          data.some(item => {
            if (item.default) {
              this.address = item
              return true
            }
          })
        })
    },
    init() {
      const address = getItem('selectAddress')
      if (address) {
        this.address = address
        removeItem('selectAddress')
      }
      if (!this.$route.params.order) return
      this.list = this.$route.params.order
      this.$nextTick(() => {
        setTimeout(() => {
          this.$refs.scroll.refresh()
        }, 200)
      })
      this.genTotal()
    },
    changeNum() {
      this.genTotal()
    },
    genTotal() {
      let total = 0
      for (let i = 0; i < this.list.length; i++) {
        const item = this.list[i]
        item.total_price = 0
        for (let j = 0; j < item.goods.length; j++) {
          total += item.goods[j].price * item.goods[j].count
          item.total_price += item.goods[j].price * item.goods[j].count
        }
      }
      this.totalPrice = total.toFixed(2)
    },
    submit() {
      if (!this.address.id) {
        this.$toast.fail('请选择收货地址')
        return
      }
      const data = this.list.map(item => {
        const copy = cloneDeep(item)
        delete copy.store
        copy.goods.forEach(goods => {
          delete goods.shop
        })
        return copy
      })
      const loading = this.$toast.loading()
      post('/shop/order/submitOrder', {
        addressid: this.address.id,
        total_price: this.totalPrice,
        // pay_channel: this.payType,
        lists: data
      })
        .then(({ data: { data } }) => {
          this.nativePay(data)
          window.payCallback = (status) => {
            console.log(status, '>>>>>>>>>>>>>>>>')
            loading.close()
            switch (status) {
              case 'SUCCESS':
                this.changeHistory(this.routeHistory.splice(0, this.routeHistory.length - 1))
                this.$router.replace('/pay/success')
                break
              case 'FAIL':
                this.$toast.fail('支付失败')
                break
              case 'CANCEL':
                this.$toast.fail('支付取消')
                break
            }
          }
        })
    },
    nativePay(data) {
      this.$jsCallNative('pay', {
        order_no: data.order_no,
        total_fee: process.env.NODE_ENV === 'development' ? 0.01 : data.total_price,
        pay_channel: this.payType
      })
    }
  }
}
</script>

